function List(props){
    //<div>N) text   ❤</div>
    let items = props.itemList.map((item, index)=>{
        return <div className="row">
            <span className="col-1">{index+1}</span>
            <div className="col-9">{item.text}</div>  
            <i onClick={props.deleteTodo} className="bi bi-trash-fill col-1"></i>
        </div>
    })
    let elem  =
    <div>
        {items}
    </div>


    return elem
}
export default List